<template>
<div class="toggle-block">
<label class="title">{{title}}</label>
 <label class="text" :class="value==0 ? 'selected': ''">{{$localizeFilter(variants[0])}}</label>
<div class="toggle-container" @click="toggle">
    <div class="toggle" :class="value==0 ? 'left' : 'right'"></div>
</div>
<label class="text" :class="value==1 ? 'selected': ''">{{$localizeFilter(variants[1])}}</label>
</div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
  props: ['title','variants','selected'],
   watch: { 
            selected: function(newVal, oldVal) {
           
                if (newVal != null && newVal!= undefined) {
                this.value = this.variants.indexOf(newVal)
            }
        }
    },
  data: ()=> ({
      value: 0
  }),
  methods: {
      toggle() {
          this.value=!this.value
          this.$emit('toggle', Number(this.value))
      }
  },

})
</script>

<style scoped>
.toggle-container {
  display: inline-block;
  border: solid 2px rgba(40, 190, 178, 1);
  width: 38px;
  height: 16px;
  border-radius: 5px;
  position: relative;
  margin: 0 5px 0 5px;
}

* {
    color: #EAEAEA;
}

.toggle {
  content: ""!important;
  background: rgba(40, 190, 178, 1);
  width: 16px;
  height: 10px;
  border-radius: 3px;
  position: absolute;
  top: 1px;
  transition: all 0.1s linear;
}

.toggle:hover {
    filter: opacity(0.7);
}

.toggle.left {
      left: 1px;
}

.toggle.right {
      left: 17px;
}
.toggle-block {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
}

.title {
    margin-right: 10px;
}

.title, .text {
    font-size: 16px;
    line-height: 19px;
    
}

.text.selected {
    font-weight: bold;
}
</style>
