<template>
<!-- TODO: refactor this component -->
<!-- separate nested modals from outer modal and remove outer modal -->
    <div class="modal-layout">
        <div class="darken" @click="closeModal()">
            <div class="modal-window" :class="(type == 'welcomeBonus' ? 'welcome-bonus' : (type == 'socialBoom' ? 'social-boom' :(type == 'operation' ? 'operation' : ''))) + (step > 1 ? 'animated' : '')" v-if="showWindow" @click.stop="">
            <div class="modal-back-button-container" :class="type=='welcomeBonus' ? 'welcome-bonus': ''">
                <button class="modal-back-button" @click="closeModal"></button>
            </div>
            
            <div class="modal-container">
                <BonusWithdrawalModal :type="type" :coin="coin" v-if="actionType=='withdrawal'" @updateModal="updateModal"  @closeModal="closeModal"/>
                <WelcomeBonusModal v-if="type=='welcomeBonus'" :data="data" @closeModal="closeModal"/>
                <SocialBoomModal v-if="type=='socialBoom'" :data="data" @closeModal="closeModal"/>
                <OperationStatusModal v-if="type=='operation'" :data="data" @closeModal="closeModal"/>
            </div>
        </div>
        </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import BonusWithdrawalModal from "@/components/modals/BonusWithdrawalModal.vue"
import WelcomeBonusModal from "@/components/modals/WelcomeBonusModal.vue"
import SocialBoomModal from "@/components/modals/SocialBoomModal.vue"
import OperationStatusModal from "@/components/modals/OperationStatusModal.vue"

export default defineComponent({
    setup() {
        
    },
    data: () => ({
        walletType: '',
        walletNumber: '',
        amount: 0,
        showWindow: true,
    }),
    props: {
        data: {
            type: Object,
            default: (()=>{
                return {}
            })
        },
        balance: {
            type: Number,
            default: 0
        },
        type: { // wallet, bonusAccount, welcomeBonus, operations
            type: String, 
            required: true
        },
        coin: {
            type: String,
            default: null
        },
        actionType: { // replenish, withdrawal
            type: String, 
            default: null
        },
        step: { // 1,2
            type: Number, 
            default: 1
        }
    },
    components: {
       /* ReplenishModal, AttentionModal,*/ BonusWithdrawalModal, WelcomeBonusModal, SocialBoomModal,OperationStatusModal
    },
    methods: {
        closeModal(reload) {
            this.$emit('closeModal', reload)
        },
        updateModal(step, walletType = '', walletNumber='', amount=0) {
            this.showWindow = false
            this.walletType = walletType
            this.walletNumber = walletNumber
            this.amount = amount
            this.$emit('updateModal', step)
            setTimeout(()=>{
                this.showWindow = true
            },0) 
        }
    }
})
</script>

