<template>
    <div class="modal-content">
        <p class="window-title">{{$localizeFilter('withdrawal')}}</p>

        <p class="window-text">{{type == 'wallet' ? $localizeFilter('modal_withdrawal_text_1_wallet', [coin]) : $localizeFilter('modal_withdrawal_text_1_account', [coin])}}</p>
          <div class="input-field">
            <input id="amount" class="custom-input" :placeholder="$localizeFilter('amount_in', [coin])" type="text" v-model.trim="amount" :class="{ invalid: v$.amount.$error }"/>
            <small class="error" v-for="(error, index) of v$.amount.$errors" :key="index">
                {{ $filters.capitalizeFirstLetter(error.$property) }}
                {{ error.$message }}
            </small>
        </div>

        <!-- <Toggle style="margin-top: 10px;" :selected="selectedMethod" :title="$localizeFilter('withdrawal_method')+':'" :variants="methods" @toggle="toggle" /> -->

        <p class="window-text">{{$localizeFilter('modal_withdrawal_text_2') + ':'}}</p>
          <div class="input-field">
            <input id="address" class="custom-input wallet" :placeholder="$localizeFilter('wallet_number', [selectedMethod])" type="text" v-model.trim="address" :class="{ invalid: v$.address.$error }"  v-maska="'TXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXX'"/>
            <small class="error" v-for="(error, index) of v$.address.$errors" :key="index">
                {{ $filters.capitalizeFirstLetter(error.$property) }}
                {{ error.$message }}
            </small>
        </div>

        <div class="modal-warning">
            <i class="material-icons right" style="color: #FF9400!important;">report</i>
            <p class="window-text warning">{{$localizeFilter('modal_withdrawal_warning', [minAmount.toFixed(0), coin, minUSDAmount.toFixed(0)])}}</p>
        </div>

        <!-- <div class="conditions-block" @click="acceptСonditions">
             <input type="checkbox" id="checkbox" class="checkbox" v-model="conditionsAccepted" @click="acceptСonditions">
             <div class="checkbox-labels-block">
                <label for="checkbox" class="conditions-label">{{$localizeFilter('modal_withdrawal_agreement')}}</label>
                <label class="conditions-hint">{{$localizeFilter('withdrawal_checkbox_hint')}}</label>
             </div>
         </div> -->

         <label class="error" v-if="notEnoughFunds">{{$localizeFilter('calculator_3_not_enough_funds', [coin])}}</label>

        <!-- :class="conditionsAccepted ? '' : 'inactive'" -->
        <button class="basic-button" @click="submitHandler">{{$localizeFilter('next')}}</button>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import useVuelidate from "@vuelidate/core";
import { required, numeric, minValue, minLength, maxLength } from "@vuelidate/validators";
import Toggle from "@/components/Toggle.vue"
import localizeFilter from '@/filters/localize.filter'

export default defineComponent({
    props: {
        type: {
            type: String, // wallet, bonusAccount
            required: true
        },
        coin: {
            type: String,
            required: true
        }
    },
    components: {
        Toggle
    },
    data: () => ({
        amount: "",
        address: "",
        // methods: ['BTC','USDT','TRON'],
        selectedMethod: 'TRON',  // 'BTC','USDT'
        notEnoughFunds: false,
        minAmount: 0,
        minUSDAmount: 0,
        // conditionsAccepted: false
    }),
    setup() {
        return { v$: useVuelidate() };
    },
    watch:{
        coin(newVal, oldVal) {
            this.$nextTick(() => { this.loadData() });
        }
    },
    validations() {
        return {
            amount: { required, numeric, minValue: minValue(this.minAmount) },
            address: { required, minLength: minLength(34), maxLength: maxLength(34) }
        };
    },
    methods: {
        async submitHandler() {
            this.v$.$touch();
            if (this.v$.$error /*|| !this.conditionsAccepted*/) {
                return;
            }
            try {
                // let method = JSON.parse(JSON.stringify(this.selectedMethod))
                // if (method=='USDT') method='USDT.TRC20'
                const formData = {amount: this.amount, withdrawWalletAddress: this.address, withdrawMethod: this.coin+'.TRC20', currencyCode: this.coin};
                await this.$store.dispatch('createWithdrawBalanceRequest', formData)
                this.$message(localizeFilter('withdraw_successfull'))
                this.$emit('closeModal', false)
                // this.$emit('updateModal', 2, this.selectedMethod, responseData.address, responseData.transferAmount)
            } catch(e) {
                if (e.message == 'Not enough funds') {
                    this.notEnoughFunds = true
                } else {
                    this.notEnoughFunds = false
                    this.$error(e)
                }
            } 
        },
        async loadData() {
            const data = {coinTicker: this.coin}
            try {
                const {minAmount, minUSDAmount} = await this.$store.dispatch('getWithdrawCredentials', data)
                // this.address = data.withdrawWalletAddress
                // this.selectedMethod = (data.withdrawMethod=='USDT.TRC20'?'USDT':'BTC')
                this.minAmount = minAmount
                this.minUSDAmount = minUSDAmount
                this.$forceUpdate()
            } catch(e) {
                this.$error(e)
            } 
        }
    // acceptСonditions() {
    //     this.conditionsAccepted = !this.conditionsAccepted
    // },
    // toggle(index) {
    //     this.selectedMethod = this.methods[index]
    // },    
    },
    async mounted() {
        this.loadData()
    }
})
</script>

